.book-navigation .menu {
  padding-top: 1em;
  padding-bottom: 0; }

.book-navigation .book-pager {
  margin: 0;
  padding: 0.5em 0;
  overflow: auto; }

.book-pager__item {
  display: inline-block;
  vertical-align: top;
  list-style-type: none; }

.book-pager__item--previous {
  width: 45%;
  text-align: left; }

[dir='rtl'] .book-pager__item--previous {
  float: right;
  text-align: right; }

[dir='rtl'] .book-pager__item--next {
  float: left;
  text-align: left; }

.book-pager__item--center {
  width: 8%;
  text-align: center; }

.book-pager__item--next {
  float: right;
  width: 45%;
  text-align: right; }
